import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import {
  Col,
  Row,
  Empty,
  Input,
  Collapse,
  Select,
  Tooltip,
  TreeSelect
} from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetServiceType/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class AssetServiceType extends Component {
  state = {
    filter: {
      id_asset_category: [],
      interval_by: [],
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAssetServiceTypes } = this.props;
    getAssetServiceTypes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetServiceType.error) {
      notification("error", this.props.assetServiceType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetServiceType.success) {
      notification("success", this.props.assetServiceType.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getAssetServiceTypes } = this.props;
    this.setState({
      filter: {
        id_asset_category: [],
        interval_by: [],
        pagination: null
      }
    });
    getAssetServiceTypes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAssetServiceTypes } = this.props;
    this.setState({
      filter: {
        id_asset_category: [],
        interval_by: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetServiceTypes();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getAssetServiceTypes } = this.props;
    //const { current_page } = this.props.assetServiceType;
    const { filter } = this.state;
    let string = "";

    if (filter.id_asset_category) {
      string += "id_asset_category=" + filter.id_asset_category;
    }
    if (filter.interval_by) {
      string
        ? (string += "&interval_by=" + filter.interval_by)
        : (string += "interval_by=" + filter.interval_by);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getAssetServiceTypes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAssetServiceTypes } = this.props;
    this.setState({
      filter: {
        id_asset_category: [],
        interval_by: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetServiceTypes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateAssetServiceType(id);
    } else {
      this.props.deactivateAssetServiceType(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getAssetServiceTypes } = this.props;
    const { search } = this.props.assetServiceType;
    const { filter } = this.props.assetServiceType;
    getAssetServiceTypes(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_asset_service_type",
    rowKey: "id_asset_service_type",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={
              <IntlMessages id="assetServiceTypes.popconfirm.deactivate" />
            }
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="assetServiceTypes.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const // Definisi kolom2 serta field2 API yang dipakai tabel
      columns = [
        {
          title: getLanguage({ id: "code" }),
          dataIndex: "asset_service_type_code",
          rowKey: "asset_service_type_code",
          width: "15%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "name" }),
          dataIndex: "description",
          rowKey: "description",
          width: "20%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "sidebar.assetCategory" }),
          dataIndex: "asset_category.asset_category_name",
          rowKey: "asset_category.asset_category_name",
          width: "20%",
          render: text => <span>{text}</span>
        },
        {
          title: getLanguage({ id: "assetServiceTypes.intervalBy" }),
          dataIndex: "interval_by",
          rowKey: "interval_by",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "primary";
              display = getLanguage({ id: "time" });
            } else if (text === 2) {
              className = "active";
              display = getLanguage({ id: "parameter" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        },
        {
          title: getLanguage({ id: "assetServiceTypes.intervalValue" }),
          dataIndex: "interval_value",
          rowKey: "interval_value",
          width: "10%",
          align: "right",
          render: text => {
            return <NumberFormat value={parseFloat(text)} />;
          }
        },
        {
          title: getLanguage({ id: "assetServiceTypes.intervalUnit" }),
          dataIndex: "interval_unit",
          rowKey: "interval_unit",
          width: "10%",
          align: "center",
          render: text => {
            if (text === "years") {
              return <IntlMessages id="years"></IntlMessages>;
            } else if (text === "months") {
              return <IntlMessages id="months"></IntlMessages>;
            } else if (text === "days") {
              return <IntlMessages id="days"></IntlMessages>;
            }
            else{
              return text
            }
          }
        },

        {
          title: getLanguage({ id: "status" }),
          dataIndex: "is_active",
          rowKey: "is_active",
          width: "10%",
          align: "center",
          render: text => {
            let className;
            let display;
            if (text === 1) {
              className = "active";
              display = getLanguage({ id: "active" });
            } else {
              className = "nonactive";
              display = getLanguage({ id: "inactive" });
            }
            return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      ];
    // console.log(this.state.filter);
    const loop = array =>
      array.map(data => {
        let perData = {
          title:
            data.asset_category_code + " (" + data.asset_category_name + ")",
          value: data.id_asset_category,
          key: data.id_asset_category,
          children: data.children ? loop(data.children) : null
        };

        return perData;
      });
    // Data setup
    const {
      asset_service_types,
      asset_categories
    } = this.props.assetServiceType;
    //console.log(this.props.assetServiceType); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (
      permissions.includes("Update Asset Service Type") &&
      columns.length === 7
    ) {
      columns.push(this.listAction);
    }

    Object.keys(asset_service_types).map((assetServiceType, index) => {
      return dataSource.push({
        ...asset_service_types[assetServiceType],
        key: assetServiceType
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.assetServiceType;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetServiceTypes.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Asset Service Type") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="assetServiceTypes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                {/* <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="assetServiceTypes.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_asset_category"
                      )}
                      value={
                        this.state.filter.id_asset_category
                          ? this.state.filter.id_asset_category
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="assetServiceTypes.filter.select.assetFiscal" />
                      }
                      loading={this.props.assetServiceType.loading}
                      allowClear={true}
                    >
                      {this.props.assetServiceType.asset_categories.map(
                        (data, key) => (
                          <Option key={key} value={data.id_asset_category}>
                            {data.asset_category_name}
                          </Option>
                        )
                      )}
                    </Select>
                  </div> */}
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="assetServiceTypes.assetCategory" />
                    </label>
                    <br />

                    <TreeSelect
                      placeholder={
                        <IntlMessages id="assetServiceTypes.select.assetCategory" />
                      }
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_asset_category"
                      )}
                      style={{ width: "95%" }}
                      allowClear={true}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      treeData={loop(asset_categories)}
                      treeDefaultExpandAll
                      loading={this.props.assetServiceType.loading}
                      value={
                        this.state.filter.id_asset_category
                          ? this.state.filter.id_asset_category
                          : []
                      }
                    />
                  </Col>
                  <Col span={12}>
                    <Row style={{ alignItems: "center", display: "flex" }}>
                      <Col span={9}>
                        <label>
                          <IntlMessages id="assetServiceTypes.intervalBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "interval_by"
                          )}
                          value={
                            this.state.filter.interval_by
                              ? this.state.filter.interval_by
                              : []
                          }
                          style={{ width: "95%" }}
                          optionFilterProp="children"
                          filterOption={true}
                          loading={this.props.assetServiceType.loading}
                          placeholder={
                            <IntlMessages id="assetServiceTypes.select.intervalBy" />
                          }
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={1}>
                            <IntlMessages id="time"></IntlMessages>
                          </Option>
                          <Option key={2} value={2}>
                            <IntlMessages id="parameter"></IntlMessages>
                          </Option>
                        </Select>
                      </Col>
                      <Col span={7}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "95%" }}
                          optionFilterProp="children"
                          filterOption={true}
                          loading={this.props.assetServiceType.loading}
                          // allowClear={true}
                          // showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <div>
                          <Button
                            size="small"
                            type="primary"
                            style={{ marginRight: "10px" }}
                            onClick={this.handleFilter}
                          >
                            <IntlMessages id="filter.submit" />
                          </Button>
                          <Button onClick={this.handleResetFilter} size="small">
                            <IntlMessages id="filter.reset" />
                          </Button>
                        </div>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {asset_service_types.length === 0 &&
            !this.props.assetServiceType.loading ? (
              <Empty
                description={
                  <IntlMessages id="assetServiceTypes.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.assetServiceType.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    assetServiceType: state.assetServiceType,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AssetServiceType);
